<template>
  <div>
    <keep-alive>
      <router-view v-if='$route.meta.keepAlive'></router-view>
    </keep-alive>
    <router-view v-if='!$route.meta.keepAlive'></router-view>

  </div>
</template>

<script>
export default {
  name: 'hollow-index',
  methods: {
    showHollowList () {
      this.$router.push({ name: 'hollowList' });
    },
    showHollowDetail (postID) {
      this.$router.push({
        name: 'hollowDetail',
        params: {
          id: postID,
        },
      })
    },
    showHollowNew () {
      this.$router.push({
        name: 'hollowNew'
      })
    },
  },
  mounted () {
    this.$bus.$on('showHollowList', this.showHollowList);
    this.$bus.$on('showHollowDetail', this.showHollowDetail);
    this.$bus.$on('showHollowNew', this.showHollowNew);
  },
  beforeDestroy () {
    this.$bus.$off('showHollowList');
    this.$bus.$off('showHollowDetail');
    this.$bus.$off('showHollowNew');
  }
}
</script>

